import React, { Component } from 'react'
import * as THREE from 'three'

export default class ThreeObject extends Component {
    //
    init(el){
        //  return
        if(!el){
            return 
        }
        //
        this.container = el

        //
        this.width=this.container.offsetWidth
        this.height=this.container.offsetHeight

        //scene
        this.scene = new THREE.Scene()

        //renderder
        this.renderer = new THREE.WebGLRenderer({
            canvas: this.container,
            alpha: true,
        })
        //
        this.renderer.setSize(this.width, this.height)
        //
        this.renderer.setPixelRatio(window.devicePixelRatio)

    }

    //
    addTexture(imageList,loadedCallback){
        //
        this.textures=[]
        //
        this.loader = new THREE.TextureLoader()
        //
        this.loader.crossOrigin = "anonymous"

        //promises
        this.promises=[]
        //
        imageList && imageList.forEach((img,i)=>{
            let promise = new Promise(resolve => {
                const image = this.loader.load( img.url,resolve )
                image.magFilter = image.minFilter = THREE.LinearFilter
                image.anisotropy =  this.renderer.capabilities.getMaxAnisotropy()
                image.generateMipmaps = false;
                this.textures.push( image )
            })
            this.promises.push(promise)
        })
    
        //
        Promise.all(this.promises).then(() => {
            loadedCallback && loadedCallback()
        })
    }

    //mesh
    addMesh(shaderObject){
        //shaderObject
        const {uniforms,customVertexShader,customFragmentShader,planeWidth=this.width,planeHeight=this.height} = shaderObject
        //
        this.vertex = customVertexShader
        this.fragment = customFragmentShader
        //
        this.material = new THREE.ShaderMaterial({
            uniforms: uniforms,
            vertexShader: this.vertex,
            fragmentShader: this.fragment,
            transparent: true,
            opacity: 1.0
        })
    
        // Plane
        this.geometry = new THREE.PlaneBufferGeometry(
            planeWidth,
            planeHeight,
            1
        )            
            
        // 
        this.mesh = new THREE.Mesh(this.geometry, this.material)
        // 
        // this.mesh.position.set(0, 0, 0)
        //
        this.scene.add(this.mesh)
    }

    //
    update() {
        //update
        requestAnimationFrame(this.update.bind(this))
        //
        this.renderer.render(this.scene, this.camera)
    }


    render() {
        return (
            <></>
        )
    }
}
