import React, { Component } from 'react'
import gsap from 'gsap'
import * as THREE from 'three'
import vertexSlider from '../../glsl/vertexSlider.glsl'
import fragmentSlider from '../../glsl/fragmentSlider.glsl'
import ThreeObject from '../../components/ThreeObject'
import { portfolioData } from '../../data/portfolioData'

export default class SliderCavas extends ThreeObject {

    //
    sliderRef=React.createRef()

    //
    componentDidMount(){
        //
        this.isAnimating = false
        //
        this.currentIndex=0
        this.init(this.sliderRef)
        //
        this.initCamera()
        //
        this.addTexture(portfolioData,()=>{
            //
            this.addMesh({
                uniforms:{
                    dispFactor: { type: "f", value: 0.0 },
                    currentImage: { type: "t", value: this.textures[0] },
                    nextImage: { type: "t", value: this.textures[1] },
                    intensity: { type: "f", value: 1.0 },
                    resolution: { type: "v4", value: new THREE.Vector4() },
                    way: { type: "f", value: 1.0 },
                },
                //
                customVertexShader:vertexSlider,
                customFragmentShader:fragmentSlider,
                //
                planeWidth:this.width,
                planeHeight:this.height
            })
            //
            this.resize()
            //
            this.bindEvents()
             //
            this.update()
        })

    }

    //
    initCamera() {
        // const fov = (180 * (2 * Math.atan(this.height / 2 / this.perspective))) / Math.PI

        // this.camera = new THREE.PerspectiveCamera(fov, this.width / this.height, 1, 10000)
        // this.camera.position.set(0, 0, this.perspective)

        // this.camera = new THREE.PerspectiveCamera(
        //     70,
        //     window.innerWidth / window.innerHeight,
        //     0.001,
        //     1000
        //   )
      
        //   this.camera.position.set(0, 0, 2)
        //   this.camera.aspect = this.width / this.height

        // const dist  = this.camera.position.z
        // const height = 1
        // this.camera.fov = 2*(180/Math.PI)*Math.atan(height/(2*dist))
    
        // this.mesh.scale.x = this.camera.aspect
        // this.mesh.scale.y = 1
    
        // this.camera.updateProjectionMatrix()

        // 
        this.camera = new THREE.OrthographicCamera(
            this.width / -2,
            this.width  / 2,
            this.height / 2,
            this.height / -2,
            1,
            1000
        )
        this.camera.position.z = 1
    }

    //
    bindEvents() {
        window.addEventListener('resize', () => {
            this.width=window.innerWidth
            this.height=window.innerHeight
            this.renderer.setSize(this.width, this.height)
            this.resize()
        })

    }

    //
    resize(){

        // 
        this.imageAspect = this.textures[0].image.height/this.textures[0].image.width
        //
        let a1,a2
        if(this.height/this.width>this.imageAspect) {
            a1 = (this.width/this.height) * this.imageAspect 
            a2 = 1
        } else{
            a1 = 1
            a2 = (this.height/this.width) / this.imageAspect
        }

        // uniforms
        this.material.uniforms.resolution.value.x = a1
        this.material.uniforms.resolution.value.y = a2

        this.renderer.render(this.scene, this.camera)
    }
     
    //slider flagtrue false
    switchBg(flag,callBack){
        // 
        if( !this.isAnimating ) {
            this.isAnimating = true
            // 
            const dist=flag? 1:-1
            //index
            this.currentIndex=(this.currentIndex+portfolioData.length+dist)%portfolioData.length
            this.material.uniforms.way.value = flag? -1.0 : 1.0
            // 
            this.material.uniforms.nextImage.value = this.textures[this.currentIndex]
            callBack && callBack()
            //
            //dispFactor0~11
            gsap.to( this.material.uniforms.dispFactor, {
                value: 1,
                ease: 'Expo.easeInOut',
                duration:1.2,
                onComplete:  ()=> {
                    // slider
                    this.material.uniforms.currentImage.value = this.textures[this.currentIndex]
                    //
                    this.material.uniforms.dispFactor.value = 0.0
                    //
                    this.isAnimating = false
                    
                }
            })
        }
    }

    //
    update() {
        //update
        requestAnimationFrame(this.update.bind(this))
        //
        this.renderer.render(this.scene, this.camera)
    }

    render() {
        return (
            <canvas id="slider" className="slider_box" ref={c=>{this.sliderRef=c}}></canvas>
        )
    }
}
